import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
Vue.prototype.$axios = axios;
//Vue.axios.defaults.baseURL = "http://localhost:3000/";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    evenements: null,
    projets: null,
    destination: [
      {
        name: "Jeunesse",
        image: [],
        url: "Jeunesse",
        alias: "J"
      },
      {
        name: "Education",
        image: [],
        url: "Education",
        alias: "E"
      },
      {
        name: "Sante",
        image: [],
        url: "Sante",
        alias: "E"
      },
      {
        name: "Solidarite",
        image: [],
        url: "Solidarite",
        alias: "S"
      },
      {
        name: "Espoire",
        image: [],
        url: "Espoire",
        alias: "E"
      }
    ]
  },

  getters: {},

  mutations: {
    // set and UPDATE
    SET_EVENT(state, data) {
      state.evenements = data;
    },

    SET_PROJET(state, data) {
      state.projets = data;
    }
  },

  actions: {
    // API call

    // Récupération tous les événements
    getAllEvent({ commit }) {
      axios
        .get("https://jesseapi.herokuapp.com/api/events/")
        .then(response => {
          let newdata = response.data;
          commit("SET_EVENT", newdata);
        })
        .catch(error => {
          error;
        });
    },

    // Récupération tous les projets
    getAllProjet({ commit }) {
      axios
        .get("https://jesseapi.herokuapp.com/api/projets/")
        .then(response => {
          let newdata = response.data;
          commit("SET_PROJET", newdata);
        })
        .catch(error => {
          error;
        });
    },
    /////////////////////////////////////////////////////////////////////////////

    // Créer un événement
    CreateOneEvent({ dispatch }, event) {
      axios
        .post("https://jesseapi.herokuapp.com/api/events/", {
          data: {
            title: event.title,
            soustitle: event.soustitle,
            texte: event.texte,
            url: event.url
          }
        })
        .then(() => {
          dispatch("getAllEvent");
        })
        .catch(error => {
          error;
        });
    },

    // Créer un projet
    CreateOneProjet({ dispatch }, projet) {
      axios
        .post("https://jesseapi.herokuapp.com/api/projets/", {
          data: {
            title: projet.title,
            description: projet.description,
            date: projet.date,
            url: projet.url
          }
        })
        .then(() => {
          dispatch("getAllProjet");
        })
        .catch(error => {
          error;
        });
    },

    ////////////////////////////////////////////////////////////////////////////////////////////////

    // Supprimer un événement
    deleteOneEvent({ dispatch }, id) {
      axios
        .delete("https://jesseapi.herokuapp.com/api/events/:", {
          data: { id }
        })
        .then(() => {
          dispatch("getAllEvent");
        })
        .catch(error => {
          error;
        });
    },

    // Supprimer un projet
    deleteOneProjet({ dispatch }, id) {
      axios
        .delete("https://jesseapi.herokuapp.com/api/projets/:", {
          data: { id }
        })
        .then(() => {
          dispatch("getAllProjet");
        })
        .catch(error => {
          error;
        });
    }
  }
});
